/**
 * Shared element/template styling
 *
 * @since 1.0.0
 */
:root {
  --fse-color-white: #fff; }

.fse-wrap {
  margin-left: auto;
  margin-right: auto;
  max-width: 87.5rem;
  max-width: var(--responsive--alignwide-width); }

.fse-cover-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.fse-has-background {
  color: var(--fse-color-white);
  color: var(--global--color-white); }
